* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
body {
    font-family: "Lato", sans-serif;
}
h1, h2, h3 {
    color: #545454;
}
h3, h4 {
    font-weight: 400;
    color:#101010;
}

.App {
    transition: all 0.5s ease;
}

.library-active {
    margin-left: 20%;
}

@import './song';
@import './player';
@import './library';
@import './nav';